*{
    box-sizing: border-box;
}
#heart{
    display: inline-block;
    margin: 100px;
    position: relative;
    animation: 0.5s heart infinite alternate-reverse;
}
@keyframes heart{
    0%{
        transform: scale(1);
    }
    100%{
        transform: scale(1.3);
    }
}
#heart>.left{

    background: red;
    width: 50px;
    height: 50px;
    position: absolute;
    transform: rotate(-45deg)
    translateY(40px);  
    bottom: 50px;
    left: -50px;
    border-radius: 50% 50% 0 0;
}
#heart>.right{

    background: red;
    width: 50px;
    height: 50px;
    position: absolute;
    transform: rotate(45deg)
    translateX(70.5px)
    translateY(-30px);  
    bottom: 50px;
    left: -50px;
    border-radius: 50% 50% 0 0;
}
#heart>.bottom{
    background: red;
    width: 50px;
    height: 50px;
    transform: rotate(45deg);
  }